import { Client } from '@stomp/stompjs';
import SockJS from 'sockjs-client';

export const API_BASE_URL = "https://port-0-beeee-mjfnlzve18281716.sel3.cloudtype.app/";

//  STOMP    pub/sub 
const WS_ENDPOINT = `${API_BASE_URL.replace(/\/$/, "")}/stomp-ws`;
const PUB_ENDPOINTS = (roomId) => [`/pub/chat/${roomId}`];
const SUB_ENDPOINTS = (roomId) => [`/sub/chat/${roomId}`];

export default class WSClient {
    constructor({ roomId, reconnectDelay = 5000 } = {}) {
        this.roomId = roomId ?? 0;
        this.reconnectDelay = reconnectDelay;
        this.client = null;
        this.subscriptions = [];
        this.connected = false;
        this.onMessage = null; //     
        this.onConnect = null; //     
        this.onDisconnect = null; //     
        this.listeners = {
            open: new Set(),
            message: new Set(),
            error: new Set(),
            close: new Set(),
        };
    }

    //  
    connect({ onMessage, onConnect, onDisconnect } = {}) {
        if (this.client?.active) return; //      

        this.onMessage = onMessage;
        this.onConnect = onConnect;
        this.onDisconnect = onDisconnect;

        const client = new Client({
            webSocketFactory: () => new SockJS(WS_ENDPOINT),
            reconnectDelay: this.reconnectDelay,
            heartbeatIncoming: 4000,
            heartbeatOutgoing: 4000,

            onConnect: (frame) => {
                console.log('[WS] Connected');
                this.connected = true;
                this._subscribe(); //   
                if (this.onConnect) this.onConnect(frame);
                this._emit("open");
            },

            onStompError: (frame) => {
                console.error('[WS] Broker reported error: ' + frame.headers['message']);
                console.error('[WS] Additional details: ' + frame.body);
                this._emit("error", new Error(frame?.headers?.message || "STOMP error"));
            },

            onWebSocketClose: () => {
                console.log('[WS] Closed');
                this.connected = false;
                if (this.onDisconnect) this.onDisconnect();
                this._emit("close");
            }
        });

        this.client = client;
        client.activate();
    }

    //   ()
    _subscribe() {
        if (!this.client || !this.client.connected) return;

        //   
        this.subscriptions.forEach(sub => sub.unsubscribe());
        this.subscriptions = [];

        //    
        SUB_ENDPOINTS(this.roomId).forEach(dest => {
            const sub = this.client.subscribe(dest, (message) => {
                if (!message.body) return;
                try {
                    const payload = JSON.parse(message.body);
                    this.onMessage?.(payload);
                    this._emit("message", payload);
                } catch (e) {
                    console.error('[WS] JSON Parse Error', e);
                    const raw = { raw: message.body };
                    this.onMessage?.(raw);
                    this._emit("message", raw);
                }
            });
            this.subscriptions.push(sub);
        });
    }

    //  
    send(messageObj) {
        if (!this.client || !this.client.connected) {
            console.warn('[WS] Cannot send message: Client not connected');
            return;
        }

        const payload = JSON.stringify(messageObj);

        PUB_ENDPOINTS(this.roomId).forEach(dest => {
            this.client.publish({
                destination: dest,
                body: payload
            });
        });
    }

    //  
    disconnect() {
        if (this.client) {
            this.client.deactivate();
            console.log('[WS] Disconnected by user');
        }
        this.connected = false;
        this.subscriptions = [];
        this._emit("close");
    }

    on(event, handler) {
        this.listeners[event]?.add(handler);
        return () => this.off(event, handler);
    }

    off(event, handler) {
        this.listeners[event]?.delete(handler);
    }

    _emit(event, ...args) {
        this.listeners[event]?.forEach((fn) => fn(...args));
    }

    //    
    static buildTalkMessage({ text, senderName = "User", chatRoomId = 0 }) {
        //  ID  (   UI )
        const tempId = `temp-${Date.now()}`;
        return {
            // id: undefined, //  
            chatRoomId: chatRoomId,
            senderUserId: 0, //   ID ( )
            senderName: senderName,
            receiverUserId: 0,
            receiverName: "",
            message: text,
            messageType: "TALK", // JOIN, TALK 
            ts: new Date().toISOString(),
            clientTempId: tempId
        };
    }

    // --- REST API Helpers ---

    static async fetchRooms() {
        try {
            const token = sessionStorage.getItem('token') || localStorage.getItem('accessToken');
            const headers = {
                "Content-Type": "application/json"
            };
            if (token) {
                headers["Authorization"] = token.startsWith("Bearer ") ? token : `Bearer ${token}`;
            }

            const res = await fetch(`${API_BASE_URL}api/chats/rooms`, {
                headers: headers
            });
            if (!res.ok) throw new Error("Failed to fetch rooms");
            return await res.json();
        } catch (e) {
            console.error(e);
            return [];
        }
    }

    static async createRoom(name = " ") {
        try {
            const res = await fetch(`${API_BASE_URL}api/chats/rooms`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ name, roomId: 0, id: "" }), // API  
            });
            if (!res.ok) throw new Error("Failed to create room");
            return await res.json();
        } catch (e) {
            console.error(e);
            return null;
        }
    }

    static async fetchChats(chatRoomId) {
        try {
            const token = sessionStorage.getItem('token') || localStorage.getItem('accessToken');
            const headers = {
                "Content-Type": "application/json"
            };
            if (token) {
                headers["Authorization"] = token.startsWith("Bearer ") ? token : `Bearer ${token}`;
            }

            const res = await fetch(`${API_BASE_URL}api/chats/${chatRoomId}`, {
                headers: headers
            });
            if (!res.ok) throw new Error("Failed to fetch chats");
            return await res.json();
        } catch (e) {
            console.error(e);
            return [];
        }
    }
}