import React from 'react';
import styles from './AuthChoice.module.css';
import logo from '../../assets/pic/TokPlan.png';
import BackgroundPattern from '../../components/Landing/BackgroundPattern';
import kakao from '../../assets/oauth/.png';
import naver from '../../assets/oauth/.png';
import google from '../../assets/oauth/.png';
import StartButton from '../../components/Landing/StartButton';
import Snow from '../../components/Landing/Snow';
import { MAIN_BASE_URL } from '../../apis/api';

const AuthChoice = () => {
    const handleSocialLogin = (provider) => {
        // /       
        // ( redirect_uri   )
        const redirectUri = `${window.location.origin}/home`;
        window.location.href = `${MAIN_BASE_URL}/oauth2/authorization/${provider}?redirect_uri=${encodeURIComponent(redirectUri)}`;
    };

    return (
        <div className={styles.container}>
            {/*     */}
            <BackgroundPattern />
            <Snow />

            <img src={logo} alt="TokPlan Logo" className={styles.logo} />

            <div className={styles.contentWrapper}>
                {/* /  */}
                <div className={styles.buttonGroup}>
                    <StartButton
                        to="/login"
                        text=""
                        style={{ width: '270px', height: '61px', justifyContent: 'space-between' }}
                        arrowStyle={{ width: '40px', height: '40px' }}
                    />
                    <StartButton
                        to="/signup"
                        text=""
                        style={{ width: '270px', height: '61px', justifyContent: 'space-between' }}
                        arrowStyle={{ width: '40px', height: '40px' }}
                    />
                </div>

                {/*    */}
                <div className={styles.socialGroup}>
                    <button
                        className={styles.socialBtn}
                        style={{ backgroundColor: '#FEE500' }}
                        onClick={() => handleSocialLogin('kakao')}
                    >
                        <img src={kakao} alt="Kakao" />
                    </button>
                    <button
                        className={styles.socialBtn}
                        style={{ backgroundColor: '#03C75A' }}
                        onClick={() => handleSocialLogin('naver')}
                    >
                        <img src={naver} alt="Naver" />
                    </button>
                    <button
                        className={styles.socialBtn}
                        style={{ backgroundColor: '#FFFFFF' }}
                        onClick={() => handleSocialLogin('google')}
                    >
                        <img src={google} alt="Google" />
                    </button>
                </div>
            </div>
        </div>
    );
};

export default AuthChoice;
